.articles-main {
  padding-top: 20px;
  &__body {
    background: #F0F4F7;
    padding: 120px 0;
    margin-top: 35px;
  }
  &__wrap {
    max-width: 900px;
    margin: 0 auto;
  }
  .pagination-wrap {
    margin-top: 80px;
  }
}

@media screen and (max-width: 992px) {
  .articles-main {
    &__body {
      padding: 60px 0 100px;
      margin-top: 30px;
    }
    .pagination-wrap {
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 576px) {
  .articles-main {
    .pagination-wrap {
      margin-top: 30px;
    }
  }
}