.contacts {
  padding-top: 20px;
  &-body {
    background: #F0F4F7;
    padding: 120px 0 505px 0;
    margin-top: 35px;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 60px;
  }
  &__left {
    width: 50%;
    max-width: 500px;
  }
  &__right {
    position: relative;
    width: 50%;
    max-width: 620px;
    background: #233C62;
    border-radius: 6px;
    padding: 60px 70px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: -500px;
      left: -200px;
      width: 650px;
      height: 650px;
      background: #1073E9;
      filter: blur(140px);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 90px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 30px;
    &-icon {
      width: 60px;
      height: 60px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #01DB7D;
      border-radius: 50%;
      img {
        max-width: 80%;
      }
    }
    &-title {
      font-size: 16px;
      line-height: 130%;
      color: #4272AC;
    }
    &-text {
      font-size: 18px;
      line-height: 130%;
    }
    a:hover {
      color: #01DB7D;
    }
  }
  &__requisites {
    margin-top: 100px;
    &-title {
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: #4272AC;
    }
    &-text {
      margin-top: 24px;
      span {
        display: inline-block;
        font-size: 18px;
        text-transform: uppercase;
        line-height: 120%;
        &:nth-child(1) {
          width: 100%;
          margin-bottom: 12px !important;
        }
        &:not(:last-child) {
          margin: 0 20px 12px 0;
        }
      }
    }
  }
  &__btn {
    height: 60px;
    gap: 20px;
    padding: 5px 40px;
    margin-top: 90px;
    i {
      width: 22px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      text-transform: uppercase;
    }
  }
  &__logo {
    position: relative;
    z-index: 2;
    max-width: 180px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__subtitle2 {
    position: relative;
    z-index: 2;
    font-weight: 700;
    text-transform: uppercase;
    color: #27F891;
    margin-top: 12px;
  }
  .connection__form {
    position: relative;
    z-index: 2;
    margin-top: 45px;
  }
  @media screen and (min-width: 993px) {
    .connection__form-grid {
      grid-template-columns: 100%;
    }
  }
  .connection__form-check {
    margin-top: 24px;
  }
  .form-bot {
    max-width: 280px;
    margin-top: 24px;
    img {
      width: 100%;
    }
  }
  .connection__form-btn {
    width: 100%;
    margin-top: 65px;
  }
}
.map-section {
  position: relative;
  z-index: 2;
  padding-bottom: 140px;
  margin-top: -335px;
  &__inner {
    position: relative;
    padding-bottom: 50%;
  }
  .map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
      border-radius: 6px;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 6px;
    }
  }
}

@media screen and (max-width: 1500px) {
  .contacts {
    &-dody {
      padding-bottom: 480px;
    }
    &__right {
      max-width: 550px;
      padding: 60px 40px;
    }
  }
  .map-section {
    padding-bottom: 120px;
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    &-body {
      padding: 60px 0 450px;
    }
    &__wrap {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      max-width: 100%;
      width: 100%;
    }
    &__items {
      margin-top: 60px;
    }
    &__requisites {
      margin-top: 60px;
    }
    &__btn {
      margin-top: 60px;
    }
  }
  .map-section {
    padding-bottom: 100px;
    &__inner {
      min-height: 680px;
    }
  }
}

@media screen and (max-width: 767px) {
  .contacts {
    &-body {
      padding-bottom: 350px;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__right {
      padding: 60px 20px;
    }
    &__btn {
      width: 100%;
      padding: 5px 10px;
    }
  }
  .map-section {
    &__inner {
      min-height: 440px;
    }
  }
}