.main2 {
  position: relative;
  z-index: 6;
  padding: 0 0 50px 0;
  &:after {
    display: none;
  }
  .main__img {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180.00deg, rgb(0, 23, 69) 33.333%,rgba(3, 0, 153, 0) 70.75%);
      opacity: 0.3;
      pointer-events: none;
    }
    img {
      width: 100%;
      height: 100%;
      min-height: 180px;
      display: block;
    }
  }
  .breadcrumbs {
    border-top: unset;
  }
  .main-form {
    background: #233C62;
    margin-top: 0;
  }
  .main-form__group {
    &:nth-child(1) {
      flex-grow: unset;
    }
  }
  .main-form__input {
    input:nth-child(3) {
      width: 50px;
    }
  }
  .main-form__btn {
    height: 40px;
    span {
      font-size: 16px;
    }
  }
  .main-form__btn2 {
    height: 40px;
    background: transparent;
    border: 1px solid #fff;
    border-radius: 6px;
    padding: 5px 40px;
    color: #fff;
    &:hover {
      background: #fff;
      color: #2A2C2F;
    }
    span {
      font-size: 16px;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  .main-form__close {
    display: none;
  }
  .main-form__bg {
    display: none;
  }
}
.questions {
  padding: 0 0 140px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 60px;
  }
  &__item {
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      background: #1073E9;
      border-radius: 6px;
      padding: 20px 30px;
      cursor: pointer;
    }
    .item-show--active {
      .questions__item-btn img {
        transform: rotate(-45deg);
      }
    }
    &-title {
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
      color: #fff;
    }
    &-btn {
      width: 15px;
      flex-shrink: 0;
      img {
        width: 100%;
        height: auto;
        transition: all 0.3s;
      }
    }
    &-hidden {
      display: none;
      background: #F0F4F7;
      border-radius: 0 0 6px 6px;
      padding: 24px 30px;
    }
    &-text {
      p {
        font-size: 18px;
        line-height: 140%;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .main2 {
    .main-form__input {
      input {
        font-size: 14px;
      }
      .input-text {
        font-size: 14px;
      }
      &-wrap {
        label {
          font-size: 14px;
        }
      }
    }
    .nice-select {
      min-width: 110px;
      font-size: 14px;
      .list {
        li {
          font-size: 14px;
        }
      }
    }
    .main-form__btn,
    .main-form__btn2 {
      padding: 5px 20px;
    }
  }
}

@media screen and (max-width: 1450px) {
  .main2 {
    @media screen and (min-width: 1361px) {
      .main-form__top {
        gap: 12px;
      }
      .main-form__btn,
      .main-form__btn2 {
        padding: 5px 10px;
        span {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .questions {
    padding-bottom: 120px;
  }
}

@media screen and (max-width: 1360px) {
  .main2 {
    .main-form__wrap {
      position: fixed;
      z-index: 5;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px 10px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    .main-form {
      position: relative;
      max-width: 680px;
      width: 100%;
      padding: 80px 30px 50px;
    }
    .main-form__top {
      flex-wrap: wrap;
    }
    .main-form__group {
      width: 100%;
    }
    .main-form__close {
      position: absolute;
      top: 30px;
      right: 30px;
      width: 25px;
      display: block;
      background: transparent;
      padding: 5px;
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
    .main-form__group:nth-child(1) {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
    }
    .nice-select {
      width: 100%;
    }
    .main-form__btns {
      width: 100%;
      margin-top: 16px;
    }
    .main-form__btn,
    .main-form__btn2 {
      width: 50%;
      height: 60px;
      padding: 5px 10px;
    }
    .main-form__bg {
      position: fixed;
      z-index: 4;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      background: rgba(4, 25, 40, 0.5);
      opacity: 0;
      pointer-events: none;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main2 {
    padding-bottom: 30px;
  }
  .questions {
    padding-bottom: 60px;
    &__items {
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 767px) {
  .main2 {
    .main__img {
      img {
        min-height: 120px;
      }
    }
    .main-form__wrap {
      padding: 0;
    }
    .main-form {
      max-width: 100%;
      width: 100%;
      height: 100%;
      overflow-y: auto;
    }
    .main-form__bg {
      display: none;
    }
    .main-form__group {
      display: grid;
      grid-template-columns: 100% !important;
    }
    .main-form__btns {
      flex-direction: column;
    }
    .main-form__btn,
    .main-form__btn2{
      width: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .main2 {
    .main-form {
      padding: 80px 10px;
    }
  }
  .questions {
    &__item {
      &-show {
        padding: 20px;
      }
      &-title {
        font-size: 18px;
      }
      &-hidden {
        padding: 20px;
      }
      &-text {
        p {
          font-size: 16px;
        }
      }
    }
  }
}

