.deal {
  padding-top: 20px;
  &-body {
    background: #F0F4F7;
    padding: 120px 0;
    margin-top: 35px;
  }
  &__wrap {
    max-width: 900px;
  }
  &__desc {
    background: #fff;
    border-radius: 6px;
    padding: 48px;
    margin-top: 20px;
    p {
      font-size: 16px;
      line-height: 140%;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  .gallery2 {
    position: relative;
    display: flex;
    align-items: stretch;
    gap: 20px;
    padding-bottom: 56.7%;
    .swiper {
      height: 100%;
    }
  }
  .gallery__small {
    position: absolute;
    top: 0;
    left: 0;
    width: 120px;
    height: 100%;
    .swiper-slide {
      cursor: pointer;
      &:hover {
        img {
          border-color: #15d878;
        }
      }
      img {
        border: 3px solid transparent;
      }
    }
    .swiper-slide-thumb-active {
      img {
        border-color: #15D878;
      }
    }
  }
  .gallery__big {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 140px);
    height: 100%;
  }
  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
      border-radius: 6px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 60px;
    height: 60px;
    background: rgba(239, 242, 245, 0.7);
    &:hover {
      background: rgba(239, 242, 245, 1);
    }
  }
  .swiper-button-prev {
    left: 20px;
  }
  .swiper-button-next {
    right: 20px;
  }
  .swiper-pagination {
    bottom: 20px;
    &-bullet {
      background: rgba(0, 0, 0, 0.5);
      opacity: 1 !important;
      &-active {
        background: #fff;
      }
    }
  }
  .articles__cards {
    max-width: 900px;
    margin-top: 100px;
  }
}

@media screen and (max-width: 992px) {
  .deal {
    &-body {
      padding: 60px 0 100px;
      margin-top: 30px;
    }
    .articles__cards {
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 768px) {
  .deal {
    .gallery2 {
      flex-direction: column;
      gap: 10px;
      padding-bottom: 0;
    }
    .gallery__big {
      position: static;
      width: 100%;
      .swiper-slide {
        padding-bottom: 70%;
        img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
    .gallery__small {
      position: static;
      width: 100%;
      .swiper-slide {
        padding-bottom: 18%;
        img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .deal {
    &__desc {
      padding: 48px 20px;
    }
    .swiper-button-prev {
      left: 10px;
      width: 34px;
      height: 34px;
    }
    .swiper-button-next {
      right: 10px;
      width: 34px;
      height: 34px;
    }
  }
}