.footer-top {
  background: #F0F4F7;
  padding: 36px 0;
  .menu {
    display: flex !important;
    flex-wrap: wrap;
    justify-content: center;
    gap: 19px 39px;
    padding: 0 !important;
    font-size: 17px;
  }
  .menu-item {
    display: block !important;
  }
  .menu-item__show {
    padding: 0 !important;
    text-decoration: underline;
    text-decoration-color: transparent;
    &:hover {
      text-decoration-color: #1073E9;
    }
    &:after {
      display: none;
    }
  }
}
.footer-center {
  &__inner {
    border-bottom: 1px solid #959697;
    padding: 56px 0 24px;
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 32px 16px;
  }
  &__logo {
    width: 185px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    color: #233C62;
    margin-top: 12px;
  }
  &__menu {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    li a {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F0F4F7;
      border-radius: 6px;
      padding: 8px 16px;
      font-size: 15px;
      line-height: 140%;
      &:hover {
        background: #CBD4E1;
      }
    }
  }
  &__group2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 100px;
    margin-top: 80px;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 32px;
  }
  &__social {
    max-width: 34px;
    &:hover {
      opacity: 0.5;
    }
    svg {
      max-width: 100%;
      width: auto;
      height: auto;
    }
  }
  &__subgroup {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 80px;
  }
  &__items {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__phone,
  &__email,
  &__location {
    font-size: 15px;
    line-height: 130%;
    &:hover {
      color: #1073E9;
      text-decoration-color: transparent;
    }
  }
  &__email {
    text-decoration: underline;
  }
  &__btn {
    height: 35px;
    padding: 5px 20px;
    span {
      font-size: 14px;
    }
  }
}
.footer-bottom {
  padding: 26px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  p {
    flex-shrink: 0;
    font-size: 13px;
    line-height: 130%;
    color: #959697;
    &:last-child {
      text-align: right;
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px 30px;
    li a {
      font-size: 13px;
      line-height: 130%;
      color: #959697;
      text-decoration: underline;
      &:hover {
        color: #1073E9;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer-center {
    &__group2 {
      gap: 40px;
    }
    &__subgroup {
      gap: 40px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .footer-center {
    &__logo {
      width: 150px;
    }
    &__menu {
      li a {
        padding: 8px 12px;
        font-size: 14px;
      }
    }
    &__group2 {
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer-center {
    &__group2 {
      flex-direction: column;
      align-items: start;
    }
    &__subgroup {
      width: 100%;
      align-items: flex-end;
    }
    &__items {
      flex-direction: column;
      align-items: start;
    }
  }
  .footer-bottom {
    padding: 36px 0;
    &__inner {
      flex-direction: column;
      text-align: center;
      gap: 3px;
      p {
        &:last-child {
          text-align: center;
        }
        br {
          display: none;
        }
      }
      ul {
        order: -1;
        gap: 8px 20px;
        margin-bottom: 40px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-center {
    &__inner {
      padding: 40px 0;
    }
    &__group {
      flex-direction: column;
    }
    &__menu {
      li a {
        padding: 8px 40px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-center {
    &__group {
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    &__logo-wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__menu {
      justify-content: center;
      li a {
        font-size: 17px;
      }
    }
  }
  .footer-center {
    &__group2 {
      align-items: center;
      text-align: center;
    }
    &__subgroup {
      flex-direction: column;
      align-items: center;
    }
    &__items {
      align-items: center;
    }
    &__btn {
      width: 100%;
      height: 50px;
      span {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .footer-center {
    &__menu {
      li a {
        padding: 8px 12px;
        font-size: 14px;
      }
    }
  }
}