.connection-main {
  padding: 50px 0 140px;
  &__text {
    max-width: 880px;
    background: #F0F4F7;
    border-radius: 6px;
    padding: 20px;
    margin-top: 40px;
    p {
      font-size: 16px;
      line-height: 140%;
    }
  }
  &__wrap {
    position: relative;
    background: #233C62;
    border-radius: 6px;
    padding: 60px;
    margin-top: 40px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: -500px;
      left: -200px;
      width: 650px;
      height: 650px;
      background: #1073E9;
      filter: blur(140px);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__desc {
    position: relative;
    z-index: 6;
    .connection-main__logo {
      display: none;
    }
  }
  &__img {
    position: absolute;
    right: -50px;
    bottom: 0;
    max-width: 570px;
    width: 100%;
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__subtitle {
    font-weight: 700;
    text-transform: uppercase;
    color: #27F891;
    margin-bottom: 45px;
  }
  .form-check {
    margin-top: 32px;
  }
  .form-bot {
    max-width: 280px;
    margin-top: 32px;
    img {
      width: 100%;
    }
  }
  .form-btn {
    margin-top: 60px;
  }
  &__logo {
    position: absolute;
    z-index: 5;
    left: -190px;
    bottom: 64px;
    max-width: 254px;
    display: block;
  }
  .circle {
    position: absolute;
    z-index: 3;
    right: -26px;
    bottom: -90px;
    width: 340px;
    height: 340px;
    background: rgba(189, 21, 216, .93);
    filter: blur(60px);
    border-radius: 50%;
    opacity: .6;
    pointer-events: none;
  }
  .circle2 {
    position: absolute;
    z-index: 2;
    right: -40px;
    bottom: -80px;
    width: 340px;
    height: 340px;
    background: rgba(21, 216, 216, 0.20);
    filter: blur(4px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle3 {
    position: absolute;
    right: 10px;
    bottom: -240px;
    width: 600px;
    height: 600px;
    background: #1073e9;
    filter: blur(300px);
    pointer-events: none;
  }
  .circle4 {
    position: absolute;
    z-index: 3;
    left: -113px;
    bottom: -63px;
    width: 163px;
    height: 163px;
    background: rgba(21, 216, 216, .23);
    filter: blur(2px);
    backdrop-filter: blur(2px);
    border-radius: 50%;
    pointer-events: none;
  }
  .circle5 {
    position: absolute;
    z-index: 1;
    left: -87px;
    bottom: 30px;
    width: 220px;
    height: 220px;
    background: rgba(21, 216, 99, .93);
    filter: blur(50px);
    border-radius: 50%;
    opacity: .6;
    pointer-events: none;
  }
  .circle6 {
    position: absolute;
    left: -65px;
    bottom: 40px;
    width: 200px;
    height: 200px;
    background: rgba(90, 255, 215, .2);
    filter: blur(12px);
    border-radius: 50%;
    pointer-events: none;
  }
}

@media screen and (max-width: 1500px) {
  .connection-main {
    padding: 20px 0 140px;
    &__text {
      margin-top: 30px;
    }
    &__desc {
      max-width: 550px;
    }
    &__logo {
      left: unset;
      right: 85px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .connection-main {
    &__img {
      max-width: 487px;
    }
  }
}

@media screen and (max-width: 992px) {
  .connection-main {
    padding-bottom: 100px;
    &__wrap {
      padding: 60px 40px;
    }
    &__desc {
      max-width: 380px;
      .connection-main__logo {
        position: static;
        display: block;
        margin-top: 45px;
      }
    }
    &__img {
      right: -140px;
    }
    .connection__form-grid {
      grid-template-columns: 100%;
    }
    &__logo {
      display: none;
    }
    .circle {
      right: 10px;
      width: 260px;
      height: 260px;
    }
    .circle2 {
      right: 10px;
      width: 260px;
      height: 260px;
    }
  }
}

@media screen and (max-width: 767px) {
  .connection-main {
    &__wrap {
      padding-bottom: 375px;
    }
    &__desc {
      max-width: 100%;
      .connection-main__logo {
        display: none;
      }
    }
    &__img {
      right: 50%;
      transform: translate(50%, 0);
      max-width: 400px;
    }
    .form-btn {
      margin-top: 32px;
    }
    &__logo {
      right: 20px;
      bottom: 20px;
      display: block;
    }
    .circle3 {
      right: 50%;
      bottom: -140px;
      transform: translate(50%, 0);
      width: 400px;
      height: 400px;
    }
    .circle4 {
      left: -50px;
      bottom: -50px;
      width: 115px;
      height: 115px;
    }
    .circle5 {
      left: -25px;
      bottom: 25px;
      width: 140px;
      height: 140px;
    }
    .circle6 {
      left: -25px;
      bottom: 25px;
      width: 140px;
      height: 140px;
    }
  }
}

@media screen and (max-width: 576px) {
  .connection-main {
    &__wrap {
      padding: 60px 20px 375px 20px;
    }
  }
}
@media screen and (max-width: 450px) {
  .connection-main {
    &__img {
      img {
        z-index: 3;
      }
    }
  }
}
