.wrapper {
  overflow: hidden;
}
.whatsapp {
  &:hover {
    svg path {
      fill: #00FF70;
    }
  }
}
.viber {
  &:hover {
    svg path {
      fill: #C07DEB;
    }
  }
}
.telegram {
  &:hover {
    svg .blue-path {
      fill: #56cafa;
    }
    svg .white-path {
      fill: #fff;
    }
  }
}
.btn-blue {
  background: #1073E9;
  border-radius: 6px;
  &:hover {
    background: #31A8FF;
  }
  span {
    line-height: 130%;
    font-weight: 500;
    color: #fff;
  }
}
.btn-blue2 {
  background: linear-gradient(180.00deg, rgb(0, 116, 255),rgb(0, 71, 177) 100%);
  background-color: #0074FF;
  border-radius: 6px;
  &:hover {
    background: #0074FF;
  }
  span {
    line-height: 130%;
    font-weight: 700;
    color: #fff;
  }
}
.btn-blue__outline {
  background: transparent;
  border: 1px solid #1073E9;
  border-radius: 6px;
  color: #1073E9;
  &:hover {
    border-color: #31A8FF;
    color: #31A8FF;
  }
  span {
    line-height: 130%;
    font-weight: 500;
  }
}
.btn-green__outline {
  background: transparent;
  border: 1px solid #01DB7D;
  border-radius: 6px;
  color: #01DB7D;
  &:hover {
    background: #01DB7D;
    color: #fff;
  }
  span {
    line-height: 130%;
    font-weight: 500;
  }
}
.btn-gray {
  background: #E2E8EF;
  border-radius: 6px;
  color: #2A2C2F;
  &:hover {
    background: #F0F4F7;
    color: #767676;
  }
  span {
    line-height: 130%;
  }
}
.btn-green {
  background: linear-gradient(0.00deg, rgb(0, 145, 58) 5.833%,rgb(0, 225, 131) 100%);
  background-color: #01E183;
  border-radius: 6px;
  &:hover {
    background: #01E183;
  }
  span {
    line-height: 130%;
    font-weight: 700;
    color: #fff;
  }
}
.h1 {
  font-size: 48px;
  line-height: 110%;
  font-weight: 700;
}
.h2 {
  font-size: 36px;
  line-height: 110%;
  font-weight: 500;
}
.h3 {
  font-size: 24px;
  line-height: 110%;
  font-weight: 500;
}
.nice-select {
  display: flex;
  align-items: center;
  background: #fff !important;
  border: none;
  padding: 0 20px 0 10px;
  font-size: 16px;
  line-height: 130%;
  font-weight: 400;
  white-space: unset;
  &:after {
    content: '';
    position: absolute;
    z-index: 2;
    top: 50%;
    right: 14px;
    transform: translate(0, -50%);
    width: 9px;
    height: 6px;
    background-image: url("../img/arrow-bottom.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }
  .current {
    color: #959697;
    &--active {
      color: #2A2C2F;
    }
  }
  .list {
    z-index: 3;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    background: #fff;
    box-shadow: unset;
    border-radius: 0 0 6px 6px;
    padding: 4px 10px 26px 10px;
    margin-top: 0;
    li {
      font-size: 16px;
      line-height: 130%;
      font-weight: 400 !important;
      background: transparent !important;
      border: none !important;
      padding: 0 !important;
      &:hover {
        color: #4272AC;
      }
    }
    .selected {
      text-decoration: underline;
      color: #4272AC;
    }
  }
}
.open {
  border-radius: 6px 6px 0 0;
  &:after {
    transform: translate(0, -50%) rotate(-180deg) !important;
  }
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  border-radius: 50%;
  &:after {
    display: none;
  }
  svg {
    max-width: 90%;
  }
}
.swiper-button-disabled {
  opacity: 1 !important;
  pointer-events: auto !important;
}
.text {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  h2 {
    font-size: 36px;
    line-height: 110%;
    font-weight: 500;
  }
  h5 {
    font-size: 15px;
    line-height: 140%;
    font-weight: 700;
  }
  p {
    font-size: 15px;
    line-height: 140%;
  }
  nav {
    h5 {
      margin-bottom: 15px;
    }
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 6px;
    li {
      position: relative;
      font-size: 15px;
      line-height: 140%;
      padding-left: 22px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 4px;
        height: 4px;
        background: #1073E9;
        border-radius: 50%;
      }
    }
  }
}
.form {
  &-group {
    position: relative;
    input {
      width: 100%;
      height: 50px;
      background: #40597E;
      border: 2px solid #40597E;
      border-radius: 6px;
      padding: 0 10px 0 0;
      font-size: 16px;
      line-height: 130%;
      font-weight: 500;
      color: #fff;
      &:focus {
        background: #40597E;
        border-color: #1073E9;
      }

    }
    .input-text {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translate(0, -50%);
      font-size: 16px;
      line-height: 130%;
      color: #fff;
      pointer-events: none;
      span {
        color: #FF0000;
      }

    }
    .name-input {
      padding-left: 69px;
    }
    .phone-input {
      padding-left: 103px;
    }
    .email-input {
      padding-left: 75px;
    }
    .city-input {
      padding-left: 82px;
    }
    .error {
      background: #6C426B;
      border-color: #FF0000;
    }
    textarea {
      width: 100%;
      height: 100px;
      background: #40597E;
      border: 2px solid #40597E;
      border-radius: 6px;
      padding: 12px 20px;
      font-size: 16px;
      line-height: 130%;
      font-weight: 500;
      color: #fff;
      text-indent: 175px;
      resize: none;
      &:focus {
        border-color: #1073E9;
      }

    }
    .textarea-text {
      position: absolute;
      top: 12px;
      left: 20px;
      font-size: 16px;
      line-height: 130%;
      color: #fff;
      pointer-events: none;
      span {
        color: #FF0000;
      }

    }
  }
  &-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &-btn {
    height: 60px;
    padding: 5px 40px;
    span {
      font-size: 16px;
      text-transform: uppercase;
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        background-image: url("../img/check.svg");
        pointer-events: auto;
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 36px;
      font-size: 16px;
      line-height: 140%;
      color: #fff;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 22px;
        height: 22px;
        background: #fff;
        border-radius: 3px;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: -4px;
        left: 5px;
        width: 24px;
        height: 19.5px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transition: all 0.125s;
        pointer-events: none;
      }
      a {
        position: relative;
        z-index: 3;
        text-decoration: underline;
        &:hover {
          text-decoration-color: transparent;
        }
      }
    }
  }
}
.up {
  position: fixed;
  z-index: 6;
  right: 80px;
  bottom: 80px;
  width: 80px;
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 5px;
  background: #F0F4F7;
  border: 2px solid rgba(66, 114, 172, 0.3);
  border-radius: 6px;
  padding: 12px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  &:hover {
    background: #c6e8ff;
  }
  span {
    font-size: 11px;
    line-height: 140%;
    text-transform: uppercase;
    color: #4272AC;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(4, 25, 40, 0.5);
  padding: 20px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    background: #fff;
    border-radius: 6px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 30px;
    right: 30px;
    width: 25px;
    background: transparent;
    padding: 5px;
    &:hover {
      svg path {
        fill: #2A2C2F;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 620px;
    padding: 60px 70px 80px;
  }
  &__logo {
    width: 184px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__title {
    font-weight: 700;
    text-transform: uppercase;
    color: #01DD7E;
    margin-top: 12px;
  }
  .modal__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 45px;
  }
  .modal__form-group {
    input,
    textarea {
      background: #EDF2F5;
      border-color: #EDF2F5;
      color: #2A2C2F;
      &:focus {
        border-color: #1073E9;
      }
    }
    .error {
      background: #F5EDED;
      border-color: #FF0000;
      &:focus {
        background: #EDF2F5;
      }
    }
    .input-text,
    .textarea-text {
      color: #959697;
    }
  }
  .modal__form-check {
    margin-top: 30px;
    label {
      color: #2A2C2F;
      &:after {
        background: transparent;
        border: 1px solid #959697;
      }
    }
  }
  .modal__form-btn {
    margin-top: 30px;
  }
}
.modal-thanks {
  .modal__dialog {
    max-width: 500px;
    padding: 88px 70px;
  }
  .modal__content {
    max-width: 335px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0 auto;
  }
  &__logo {
    width: 184px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__title {
    font-weight: 700;
    text-transform: uppercase;
    color: #01DD7E;
    margin-top: 45px;
  }
  &__subtitle {
    font-size: 15px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #4272AC;
    margin-top: 35px;
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    color: #657791;
    margin-top: 10px;
  }
  &__btn {
    height: 60px;
    padding: 5px 40px;
    margin-top: 45px;
    span {
      font-size: 16px;
      text-transform: uppercase;
    }
  }
}
.modal-choose {
  .modal__dialog {
    max-width: 1000px;
    padding: 50px;
  }
  &__tabs-head {
    display: flex;
    align-items: stretch;
    gap: 4px;
  }
  &__tab {
    height: 35px;
    background: transparent;
    border: 1px solid #4272AC;
    border-radius: 6px;
    padding: 5px 20px;
    font-size: 16px;
    line-height: 130%;
    color: #4272AC;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: #4272AC;
      color: #fff;
    }
  }
  .tab--active {
    background: #4272AC;
    color: #fff;
  }
  &__tabs-body {
    margin-top: 60px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__title {
    text-transform: uppercase;
  }
  &__items {
    max-width: 330px;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 0 40px;
    margin-top: 30px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    li a {
      font-size: 15px;
      line-height: 120%;
      text-decoration: underline;
      text-decoration-color: transparent;
      color: #4272AC;
      &:hover {
        text-decoration-color: #4272AC;
      }
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 5px 30px;
    margin-top: 30px;
    .modal-choose__list {
      li a {
        color: #2A2C2F;
        &:hover {
          color: #4272AC;
        }
      }
    }
  }
  &__items3 {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 5px 30px;
    margin-top: 30px;
    .modal-choose__list {
      li a {
        color: #2A2C2F;
        &:hover {
          color: #4272AC;
        }
      }
    }
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 30px 16px;
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 10px;
  }
  &__btn {
    height: 35px;
    padding: 5px 40px;
    span {
      font-size: 16px;
      font-weight: 500;
    }
  }
  &__img {
    margin-top: 25px;
    img {
      width: 100%;
    }
  }
}
.breadcrumbs {
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 16px 50px;
    border-top: 1px solid #000;
    padding: 30px 0;
  }
  &__item {
    font-size: 14px;
    line-height: 130%;
    color: #787B80;
    text-decoration: underline;
    &:not(:last-child) {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -24px;
        transform: translate(0, -50%);
        width: 1px;
        height: 18px;
        background: #787b80;
        pointer-events: none;
      }
    }
    &--active {
      text-decoration-color: transparent;
      &:hover {
        color: #787B80 !important;
      }
    }
    &:hover {
      color: #1073E9;
    }
  }
}
.pagination {
  display: flex;
  align-items: center;
  gap: 60px;
  &-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
  }
  &-prev,
  &-next {
    width: 50px;
    height: 50px;
    background: #fff;
    border-radius: 6px;
    &:hover {
      background: #e2e8ef;
    }
    svg {
      max-width: 80%;
      width: auto;
      height: auto;
    }
  }
  &-items {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &-item {
    width: 50px;
    height: 50px;
    background: #fff;
    border-radius: 6px;
    font-size: 15px;
    line-height: 105%;
    &--active {
      background: #01DB7D;
      color: #fff;
      &:hover {
        background: #01DB7D;
      }
    }
    &:hover {
      background: #e2e8ef;
    }
  }
  &-btn {
    height: 50px;
    background: #CBD4E1;
    padding: 5px 40px;
    &:hover {
      background: #e2e8ef;
    }
    span {
      font-size: 14px;
      font-weight: 500;
    }
  }
}


@media screen and (max-width: 1600px) {
  .up {
    right: 20px;
  }
}

@media screen and (max-width: 1400px) {
  .h1 {
    font-size: 36px;
  }
  .modal-choose {
    .modal__dialog {
      max-width: 900px;
    }
  }
}

@media screen and (max-width: 992px) {
  .text {
    h5 {
      font-size: 17px;
    }
    p {
      font-size: 17px;
    }
    ul {
      li {
        font-size: 17px;
      }
    }
  }
  .up {
    display: none;
  }
  .modal-choose {
    &__items2 {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
    }
  }
  .pagination {
    width: 100%;
    justify-content: space-between;
    gap: 10px;
    &-wrap {
      flex-direction: column;
      gap: 30px;
    }
    &-btn {
      width: 100%;
      order: -1;
    }
  }
}

@media screen and (max-width: 767px) {
  .form {
    &-group {
      @media screen and (min-width: 451px) {
        input {
          font-size: 20px;
        }
        .input-text {
          font-size: 20px;
        }
        .name-input {
          padding-left: 79px;
        }
        .phone-input {
          padding-left: 122px;
        }
        .email-input {
          padding-left: 89px;
        }
        .city-input {
          padding-left: 96px;
        }
        textarea {
          font-size: 20px;
          text-indent: 219px;
        }
        .textarea-text {
          font-size: 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .h1 {
    font-size: 28px;
  }
  .modal-form {
    .modal__dialog {
      padding: 60px 20px 80px;
    }
  }
  .modal-thanks {
    .modal__dialog {
      padding: 80px 20px;
    }
  }
  .modal-choose {
    .modal__dialog {
      padding: 80px 20px;
    }
    &__tab {
      padding: 5px 32px;
      font-size: 18px;
    }
    &__list {
      li a {
        font-size: 18px;
      }
    }
    &__items3 {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    }
    &__top {
      flex-direction: column;
      align-items: start;
    }
    &__btns {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
    &__btn {
      height: 50px;
      padding: 5px 10px;
      span {
        font-size: 18px;
      }
    }
  }
  .pagination {
    .deleting-mobile {
      display: none;
    }
  }
}

@media screen and (max-width: 450px) {
  .modal-choose {
    &__tabs-head {
      display: grid;
      grid-template-columns: 100%;
    }
    &__tabs-body {
      margin-top: 30px;
    }
    &__items {
      grid-template-columns: 100%;
    }
    &__list {
      li a {
        font-size: 15px;
      }
    }
    &__items2 {
      grid-template-columns: 100%;
    }
    &__items3 {
      grid-template-columns: 100%;
    }
    &__btns {
      grid-template-columns: 100%;
    }
  }
  .pagination {
    &-prev,
    &-next {
      width: 40px;
      height: 40px;
    }
    &-item {
      width: 40px;
      height: 40px;
    }
  }
}

@media screen and (max-width: 359px) {
  .h2 {
    font-size: 28px;
  }
  .h3 {
    font-size: 20px;
  }
  .text {
    h2 {
      font-size: 28px;
    }
  }
  .pagination {
    &-prev,
    &-next {
      width: 36px;
      height: 36px;
    }
    &-item {
      width: 36px;
      height: 36px;
    }
  }
}