.object-main {
  padding: 50px 0 40px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  &__desc {
    max-width: 550px;
    width: 43.5%;
  }
  .gallery3 {
    position: relative;
    width: 56.5%;
    display: flex;
    align-items: stretch;
    gap: 20px;
    padding-bottom: 38.5%;
    .swiper {
      height: 100%;
    }
  }
  .gallery__big {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 100px);
    height: 100%;
    .swiper-button-prev,
    .swiper-button-next {
      width: 60px;
      height: 60px;
      background: rgba(239, 242, 245, 0.7);
      &:hover {
        background: rgba(239, 242, 245, 1);;
      }
    }
  }
  .gallery__small {
    position: absolute;
    top: 0;
    left: 0;
    width: 90px;
    height: 100%;
    padding: 23px 0;
    .swiper-slide {
      cursor: pointer;
      &:hover {
        img {
          border-color: #15d878;
        }
      }
      img {
        border: 3px solid transparent;
      }
    }
    .swiper-slide-thumb-active {
      img {
        border-color: #15D878;
      }
    }
    .swiper-button-prev {
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 18px;
      height: auto;
      margin: 0;
      &:hover {
        svg path {
          fill: #767676;
        }
      }
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
    .swiper-button-next {
      top: unset;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 18px;
      height: auto;
      margin: 0;
      &:hover {
        svg path {
          fill: #767676;
        }
      }
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
  }
  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
      border-radius: 6px;
    }
  }
  &__title {
    font-size: 24px;
    line-height: 130%;
    font-weight: 500;
  }
  &__group {
    max-width: 430px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-top: 30px;
  }
  &__price {
    font-size: 36px;
    line-height: 110%;
    font-weight: 700;
    color: #1073E9;
  }
  &__size {
    font-size: 20px;
    line-height: 130%;
    color: #4272AC;
  }
  &__location {
    font-size: 15px;
    line-height: 120%;
    margin-top: 40px;
  }
  &__metro-wrap {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 12px;
  }
  &__metro {
    display: flex;
    align-items: center;
    gap: 10px;
    i {
      width: 22px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 15px;
      line-height: 120%;
    }
  }
  &__time {
    position: relative;
    font-size: 15px;
    line-height: 120%;
    color: #4272AC;
    padding-left: 12px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 16px;
      background: #959697;
    }
  }
  &__map {
    position: relative;
    font-size: 15px;
    line-height: 120%;
    color: #1073E9;
    text-decoration: underline;
    padding-left: 12px;
    &:hover {
      text-decoration: transparent;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 16px;
      background: #959697;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px 32px;
    margin-top: 40px;
    li {
      position: relative;
      font-size: 15px;
      line-height: 120%;
      &:last-child {
        &:after {
          display: none;
        }
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -15px;
        transform: translate(0, -50%);
        width: 1px;
        height: 16px;
        background: #959697;
      }
      strong {
        font-weight: 700;
      }
    }
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 5px;
    margin-top: 16px;
  }
  &__info-item {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    padding: 5px 20px;
    font-size: 15px;
    line-height: 120%;
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
    margin-top: 50px;
  }
  &__btn {
    height: 60px;
    padding: 5px;
    span {
      font-size: 16px;
      text-transform: uppercase;
    }
  }
  .btn-blue2 {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      right: -10px;
      top: 0;
      width: 10px;
      height: 100%;
      background: transparent;
      opacity: 0;
    }
    .btn-hidden {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
      min-height: 60px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;
      background: #fff;
      border: 1px solid #15D878;
      border-radius: 6px;
      padding: 4px 12px;
      opacity: 0;
      pointer-events: none;
    }
    .btn-hidden__title {
      display: block;
      font-size: 15px;
      line-height: 130%;
      font-weight: 700;
      text-transform: capitalize;
      color: #2A2C2F;
    }
    .btn-hidden__phones {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
    .btn-hidden__phone {
      font-size: 15px;
      line-height: 130%;
      font-weight: 400;
      color: #4272AC;
      margin-top: 4px;
      &:hover {
        color: #1073E9;
      }
    }
  }
  .show-number {
    .btn-hidden {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__group2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-top: 40px;
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__item {
    position: relative;
    width: 54px;
    height: 54px;
    background: #F0F4F7;
    border-radius: 50%;
    &:hover {
      background: #c6e8ff;
      .share-hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .favorite {
    cursor: pointer;
    &--active {
      svg path {
        fill: #FF0000;
        stroke: #FF0000;
      }
    }
    svg {
      width: 22px;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  .message {
    position: relative;
  }
  .message-show {
    height: 40px;
    border-color: #4272AC;
    border-radius: 20px;
    color: #4272AC;
    padding: 5px 50px;
    &:hover {
      border-color: #1073e9;
      color: #1073e9;
    }
    span {
      font-size: 15px;
    }
  }
  .share-hidden {
    position: absolute;
    z-index: 6;
    top: 35px;
    left: -75px;
    width: auto;
    background: #fff;
    border: 1px solid #00E183;
    border-radius: 6px;
    padding: 16px 20px;
    opacity: 0;
    pointer-events: none;
  }
  .share__items {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  .share__item {
    width: 30px;
    display: block;
    transition: all 0.3s;
    &:hover {
      opacity: 0.6;
    }
    img {
      width: 100%;
    }
  }
}
.object-info {
  position: relative;
  z-index: 2;
  background: #fff;
  padding: 40px 0 140px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  &__left {
    width: 56.5%;
  }
  &__right {
    width: 43.5%;
  }
  &__title {
    text-transform: uppercase;
    color: #4272AC;
  }
  &__group {
    &:not(:first-child) {
      margin-top: 80px;
    }
  }
  &__text {
    margin-top: 40px;
    p {
      font-size: 16px;
      line-height: 140%;
    }
  }
  &__location {
    font-size: 15px;
    line-height: 120%;
    font-weight: 500;
    margin: 40px 0 20px;
    span {
      display: block;
      color: #1073E9;
      margin-bottom: 12px;
    }
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 4px;
  }
  &__btn {
    height: 35px;
    padding: 5px 20px;
    span {
      font-size: 16px;
    }
  }
  .btn-blue {
    background: #4272AC;
    &:hover {
      background: #1073E9;
    }
  }
  .btn-blue__outline {
    border-color: #4272AC;
    color: #4272AC;
    &:hover {
      border-color: #1073E9;
      color: #1073E9;
    }
  }
  .map__wrap {
    position: relative;
    padding-bottom: 100%;
    margin-top: 40px;
  }
  .map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
      border-radius: 6px;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 6px;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 32px;
    li {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
      padding: 8px 10px;
      &:nth-child(odd) {
        background: #F0F4F7;
      }
      &:nth-child(even) {
        background: #fff;
      }
    }
    li span {
      font-size: 16px;
      line-height: 140%;
    }
    li a {
      font-size: 16px;
      line-height: 140%;
      text-decoration: underline;
      color: #4272AC;
      &:hover {
        text-decoration: transparent;
      }
    }
  }
}


@media screen and (max-width: 1500px) {
  .object-main {
    &__title {
      font-size: 20px;
    }
    &__group {
      margin-top: 20px;
    }
    &__price {
      font-size: 32px;
    }
    &__location {
      margin-top: 36px;
    }
    &__btns {
      margin-top: 40px;
    }
    &__group2 {
      margin-top: 30px;
    }
    .message-show {
      padding: 5px 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .object-main {
    padding: 20px 0 30px;
    &__inner {
      flex-direction: column;
      gap: 30px;
    }
    &__desc {
      max-width: 100%;
      width: 100%;
    }
    .gallery3 {
      width: 100%;
      flex-direction: column;
      gap: 10px;
      padding-bottom: 0;
    }
    .gallery__big {
      position: static;
      width: 100%;
      .swiper-slide {
        padding-bottom: 79%;
        img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
    .gallery__small {
      position: relative;
      top: unset;
      left: unset;
      width: 100%;
      padding: 0 45px;
      .swiper-slide {
        padding-bottom: 15%;
        img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
      .swiper-button-prev {
        top: 50%;
        left: 15px;
        transform: translate(0, -50%) rotate(-90deg);
      }
      .swiper-button-next {
        top: 50%;
        left: unset;
        right: 15px;
        transform: translate(0, -50%) rotate(-90deg);
      }
    }
    .share-hidden {
      left: 0;
    }
  }
  .object-info {
    padding: 30px 0 100px;
    &__inner {
      flex-direction: column;
      gap: 30px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
    }
    &__group {
      &:not(:first-child) {
        margin-top: 60px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .object-main {
    &__desc {
      display: flex;
      flex-direction: column;
      align-items: start;
    }
    &__title {
      font-size: 24px;
    }
    &__btns {
      width: 100%;
      order: 5;
    }
    &__group2 {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .object-main {
    &__desc {
      position: relative;
      padding-bottom: 80px;
    }
    &__btns {
      grid-template-columns: 100%;
    }
    .share__items {
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
    }
    .message {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      &-show {
        width: 100%;
      }
    }
    .gallery__big {
      .swiper-button-prev,
      .swiper-button-next {
        width: 32px;
        height: 34px;
      }
    }
    .gallery__small {
      padding: 0;
      .swiper-button-prev,
      .swiper-button-next {
        display: none;
      }
    }
  }
  .object-info {
    .object-main__metro-wrap {
      position: relative;
      padding-top: 30px;
    }
    .object-main__metro {
      position: absolute;
      top: 0;
      left: 0;
    }
    .object-main__time:nth-child(2) {
      padding: 0;
      &:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .object-info {
    .object-main__metro-wrap {
      flex-direction: column;
      align-items: start;
      padding: 0;
    }
    .object-main__metro {
      position: static;
    }
    .object-main__time {
      padding: 0;
      &:after {
        display: none;
      }
    }
  }
}